import React,{Component} from 'react';
import PropTypes from 'prop-types';

class Sct1_1_bg extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <div className='bg' style={{
                backgroundImage: `url('/ReactBlog/${this.props.source}')`,
            }}>
            </div>
        );
    }
}


Sct1_1_bg.propTypes = {
    source: PropTypes.string
};

export default Sct1_1_bg;